import uniIcon from "@/components/uni-icons/uni-icons.vue"
export default {
	data() {
		return {
			delBtnWidth: 160,
			data: [{
				id: 1,
				address: '深圳市福田区',
				name: 'zhang',
				phone: '123456',
				right: 0
			}, {
				id: 2,
				address: '深圳市福田区',
				name: 'aaa',
				phone: '12322456',
				right: 0
			}],
			isScroll: true,
			windowHeight: 0,
			startX: 0
		}
	},
	onLoad() {
		let that = this;
		uni.getSystemInfo({
			success: function(res) {
				that.windowHeight = res.windowHeight;
			}
		})
	},
	components: {
		uniIcon
	},
	methods: {
		stopDraw(item) {//点击删除时不缩回去
			item.right = this.delBtnWidth
		},
		drawPrint() { //点击任意屏幕删除缩回去
			for(let index in this.data) {
				this.data[index].right = 0;
			}
		},
		gotoAddAddress() {
			uni.navigateTo({
				url: './editAddress/editAddress'
			})
		},
		drawStart(e, item) {
			let touch = e.touches[0]

			for(let index in this.data) {
				this.data[index].right = 0;
			}
			this.startX = touch.clientX
		},
		drawMove(e, item) {
			let touch = e.touches[0]
			let disX = this.startX - touch.clientX
			if (disX >= 20) {
				if (disX > this.delBtnWidth) {
					disX = this.delBtnWidth
				}
				item.right = disX
				this.isScroll = false
			} else {
				item.right = 0
				this.isScroll = true
			}
		},
		drawEnd(e, item) {
			if (item.right >= this.delBtnWidth / 2) {
				item.right = this.delBtnWidth
				this.isScroll = true
			} else {
				item.right = 0;
				this.isScroll = true
			}

		}
	}
}
